@import './variables.css';

:root{
    --desktop: 1200px;
    --laptop: 1024px;
    --tablet: 768px;
    --phone: 480px;
}

.Header{
    background-color: var(--bg-dark);
    width: var(--max-width);
    height: 4.5rem;
    position: sticky;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999;
}
.nav-controll{
    display: flex;
    flex-direction: row;
    width: 95%;
    justify-content: space-between;
    align-items: center;
    position: relative;
}
