:root{
    --desktop: 1200px;
    --laptop: 1024px;
    --tablet: 768px;
    --phone: 480px;

}

.video-player-main-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.video-player-container{
    display: flex;
    flex-direction: row;
    width: 90%;
    margin-top: 1rem;
    margin-bottom: 1rem;
}
.video-items-container{
    display: flex;
    flex-direction: column;
    width: 70%;
}
.video-player{
    width: 100%;
    height: 550px;
}
.video-items-container > p{
    font-weight: bold;
    cursor: pointer;
}
.video-items-container > button{
    background-color: #222831;
    cursor: pointer;
    padding: 1rem;
    border-radius: 5px;
    width: 8rem;
    margin-top: 1rem;
    color: white;
    border: 1px solid black;
    font-weight: 500;
}


.videoList-container{
    display: flex;
    cursor: pointer;
    margin-left: 1rem;
    margin-bottom: 1rem;
    width: 95%;
 }
.videoList-container img{
    width: 15rem;
    height: 10rem;
}
.videoList-container > div{

    display: flex;
    flex-direction: column;
    margin-left: 1rem;
    width: 16.5rem;
    height: 10rem;
}
.videoList-container > div > h3{
    margin-bottom: 0;
}
.videoList-container > div > p > span{
    font-size: smaller;
    line-height: 1rem;
}
.video-sidebar-container{
    width: 40%;
    display: flex;
    flex-direction: column;
    position: relative;
}
@media (max-width: 1024px) {
    .video-items-container{
        width: 100%;
    }
    .video-player-container{
        flex-direction: column;
        width: 90%;

    }
    .videoList-container > div > h3{
        font-size: 1em;
    }
    .videoList-container > div > p > span{
        display: none;
    }
    .videoList-container > div > p{
        display: none;
    }
    .videoList-container{
        margin-top: 3rem;
        width: 40rem;
    }
}
@media (max-width: 768px) {
    .video-player-main-container{
        width: 100%;
        justify-content: center;
    }

    .video-player-container{
        flex-direction: column;
        width: 90%;

    }
}