@import url(https://fonts.googleapis.com/css2?family=Cabin&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;600;700;800&display=swap);
@import url(/variable.css);
:root{
    /* Container width */
--max-width: 100%;

    /* Background colors */
--bg-primary: #f8f8ff;
--bg-secondary: #393e46;
--bg-light: #dbd8e3;
--bg-dark: #222831;

    /* Text colors */
--text-primary: #000105;
--text-secondary: #f8f8ff;
--text-light: #053f82;
--text-dark:  #011447;

    /* Fonts */
--font-primary: 'Cabin', sans-serif;
--font-secondary: 'Open Sans', sans-serif;
}
body{
    margin: 0;
    padding: 0;
}
.Search{
    background-color: var(--bg-dark);
    color: var(--text-secondary);
    display: flex;
    width: var(--max-width);
    height: 12rem;
    flex-direction: column;
    align-items: center;
}
.search-icon{
    width: 21px;
    
}
h1{
    font-family: var(--font-secondary);
    font-weight: 900;
    font-size: 1.9rem;
    line-height: 0ch;
}
p{
    font-family: var(--font-secondary);
    line-height: 0ch;
    font-size: medium;
    margin-bottom: 2rem;
}
form{
    background-color: transparent;
    width: 40%;
    height: 2.5rem;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
    border: 2px solid white;
}
form > input{
    width: 100%;
    border: none;
    background-color: transparent;
    outline: none;
    padding: 0 1rem;
    font-size: large;
    color: white;
}
form > button {
    width: 3rem;
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    background-color: transparent;
    border: none;
    color: white;
}
@media (max-width: 480px) {
    form{
        height: 2rem;
    }
}
.Homepage-container{
    min-height: 120vh;
}

:root{
    --desktop: 1200px;
    --laptop: 1024px;
    --tablet: 768px;
    --phone: 480px;
}

.Header{
    background-color: var(--bg-dark);
    width: var(--max-width);
    height: 4.5rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999;
}
.nav-controll{
    display: flex;
    flex-direction: row;
    width: 95%;
    justify-content: space-between;
    align-items: center;
    position: relative;
}
